//import Navbar from "./components/Navbar/Navbar";
import Home from "./components/pages/Home";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import React, { Suspense } from "react";

import { Route, Routes, Navigate } from "react-router-dom";
//import AllJobs from "./components/pages/AllJobs";
//import Sign from "./components/pages/Sign";
//import AllCompanies from "./components/pages/AllCompanies";
//import DetailsAboutCompany from "./components/pages/DetailsAboutCompany";
import Layout from "./components/Navbar/Layout";

//import { AuthContext } from "./components/store/auth-login";
//import { useContext } from "react";
import AlgoDsa from "./components/AlgoDsa/AlgoDsa";

//import Compiler from "./components/code-compiler/Compiler";
import Login from "./components/testAuth/Login";

import ForgotPassword from "./components/ForgotPassword";
import PasswordReset from "./components/PasswordReset/PasswordReset";

import AdminCrud from "./adminDashboard/AdminCrud";

import UpdateCompanyData from "./adminDashboard/UpdateCompanyData";
//import Comments from "./UI/comments/Comments";
import CommentMain from "./UI/comment/CommentMain";
import CourseHome from "./components/courses/CourseHome";
import Course from "./components/courses/Course";
import CourseLesson from "./components/courses/CourseLesson";
import AdminJobsDetails from "./adminDashboard/AdminJobDetails";
import ResumeAnalyzer from "./resumeFunction/Resume/ResumeAnalyzer";
import OpenAi from "./components/OpenAi/OpenAi";
import AdminCourseDetails from "./adminDashboard/AdminCourseDetails";
import UpdateCourse from "./adminDashboard/UpdateCourse";
//import Comment from "../src/UI/Comment";

//import Demo from "./admin/Demo";
const Signup = React.lazy(() => import("./components/testAuth/Signup"));
const AllCompanies = React.lazy(() =>
  import("./components/pages/AllCompanies")
);
const AllJobs = React.lazy(() => import("./components/pages/AllJobs"));
const DetailsAboutCompany = React.lazy(() =>
  import("./components/pages/DetailsAboutCompany")
);

const Compiler = React.lazy(() =>
  import("./components/code-compiler/Compiler")
);
const Main = React.lazy(() => import("./components/testAuth/Main"));
const Sign = React.lazy(() => import("./components/pages/Sign"));
const LoginAdmin = React.lazy(() => import("../src/admin/LoginAdmin"));

const Jobs = React.lazy(() => import("./components/Jobs/Jobs"));

function App() {
  let content = (
    <Layout>
      <Suspense fallback={<p></p>}>
        <Routes>
          <Route path="/" exact element={<Navigate replace to="/login" />} />
          <Route path="/login" element={<Login />}></Route>
          <Route path="/signup" element={<Signup />} />
          {/* <Route path="/users/:id/verify/:token" element={<EmailVerify />} /> */}
          <Route path="/forgot-password" element={<ForgotPassword />} />
          <Route
            path="/password-reset/:id/:token"
            element={<PasswordReset />}
          />

          <Route path="*" element={<Navigate to="/login" replace />} />
          {/* this is for clearing previous routes */}
        </Routes>
      </Suspense>
    </Layout>
  );

  const user = localStorage.getItem("token");
  if (user) {
    content = (
      <Layout>
        <Suspense
          fallback={
            <div className="center">
              <p></p>
            </div>
          }
        >
          <Routes>
            {user && <Route path="/" exact element={<Home />}></Route>}
            <Route path="/logout" exact element={<Main />}></Route>
            <Route path="/signin" element={<Sign />}></Route>
            <Route path="/allcomapnies" element={<AllCompanies />} />
            <Route path="/alljobs" element={<AllJobs />} />
            <Route path="/company/:id" element={<DetailsAboutCompany />} />

            <Route
              path="/company/:id/:problemId/compiler"
              element={<Compiler />}
            />
            <Route
              path="/admin/data"
              element={<LoginAdmin requiredRole="admin" />}
            />
            <Route path="/alljobs/jobs-page" element={<Jobs />} />
            <Route path="/admin/data/add-company" element={<AdminCrud />} />
            <Route path="/admin/data/add-jobs" element={<AdminJobsDetails />} />
            <Route
              path="/admin/data/add-course"
              element={<AdminCourseDetails />}
            />
            <Route
              path="/admin/data/update/:cId"
              element={<UpdateCompanyData />}
            />
            <Route
              path="/admin/data/updateCourse/:cId"
              element={<UpdateCourse />}
            />
            <Route path="/company-review/comment" element={<CommentMain />} />

            {/*here is our course page */}
            <Route path="/coursePage" element={<CourseHome />} />
            <Route path="/courses/:courseId" element={<Course />} />
            <Route
              path="/courses/:courseId/lessons/:lessonId"
              element={<CourseLesson />}
            />
            <Route path="/algo-dsa" element={<AlgoDsa />} />
            <Route
              path="/alljobs/:id/resume-analyzer"
              element={<ResumeAnalyzer />}
            />
            <Route path="/TechPlacement-Ultimate-AI" element={<OpenAi />} />
            {/* <Route path="/details" element={<Details />} /> */}
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </Suspense>
      </Layout>
    );
  }

  return (
    <>
      <div className="flex-wrapper mt-5">
        {content}
        <ToastContainer position="top-center" autoClose={1000} />
      </div>
    </>
  );
}

export default App;
